
* {
    box-sizing: border-box;
}

body {
  margin: 0;
 font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
               monospace;
 font-size: 13.333px;
 line-height: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    overflow: hidden;
    background: #111;
    color: #ddd;
}

.outline-text {
    text-shadow: -1px -1px #111, 1px -1px #111, 1px 1px #111, -1px 1px #111;
}
.active-outline {
}
.active-outline:before {
    content: '';
    position: absolute;
    left: 1px;
    top:1px;
    right: 1px;
    bottom: 1px;
    outline: solid 4px white;
}
.active-outline:after {
    content: '';
    position: absolute;
    left: 0;
    top:0;
    right: 0;
    bottom: 0;
    outline: solid 2px #111;
}

.button {
    background: #444;
    display: inline-block;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 4px;
    margin-bottom: 8px;
    margin-right: 8px;
    cursor: pointer;
    user-select: none;
}
.button:hover {
    background: #555;
}
.toggle {
    position: relative;
    padding-left: 24px;
}
.toggle:after {
    content: '';
    position: absolute;
    left: 8px;
    top: 11px;
    width: 8px;
    height: 8px;
    background: #eee;
    border-radius: 3px;
}
.toggle.active,
.button.active {
    background: #eee;
    color: #222;
}
.toggle.active:after {
    background: #222;
}

.keyname {
    background: #ccc;
    color: #222;
    padding-left: 4px;
    padding-right: 4px;
}
.keytip {
    color: #aaa;
}
.heading {
    border-top: solid 1px #666;
    padding-top: 8px;
}

.toggle.active .keyname {
    background: #aaa; 
}

